import '@babel/polyfill'
import 'mutationobserver-shim'
import Vue from 'vue'
import './plugins/bootstrap-vue'
import App from './App.vue'
import './style/style.stylus'
// 必要引用
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

// 根據 icon 的種類引用
import { faSearch, faGuitar, faBell, faAlignRight } from '@fortawesome/free-solid-svg-icons'
import { faFacebookSquare, faInstagramSquare, faLine, faYoutube } from '@fortawesome/free-brands-svg-icons'
library.add(faSearch, faGuitar, faBell, faAlignRight, faFacebookSquare, faInstagramSquare, faLine, faYoutube)

Vue.component('font-awesome-icon', FontAwesomeIcon)

new Vue({
  render: h => h(App)
}).$mount('#app')

window.addEventListener('scroll', () => {
  const oTop = document.querySelector('.drum').offsetTop
  const scrollTop = document.documentElement.scrollTop ||
  document.body.scrollTop ||
  document.querySelector('.navbar-default').scrollTop
  if (scrollTop > oTop) {
    document.querySelector('.navbar-default').classList.add('sticky')
  } else {
    document.querySelector('.navbar-default').classList.remove('sticky')
  }
  console.log(scrollTop)
}, true)
