<template>
    <section class="formSection">
        <b-container class="p-0">
        <b-card class="from border-info">
            <b-form-group
            label-cols-lg="2"
            label="Shipping Address"
            label-size="lg"
            label-class="font-weight-bold p-0"
            class="mb-0 form-title"
            >
            <b-form-group
                label-cols-sm="2"
                label="Street :"
                label-align-sm="right"
                label-for="nested-street"
            >
                <b-form-input class="w-75" :class="borderColor" v-model="street" id="nested-street"></b-form-input>
            </b-form-group>

            <b-form-group
                label-cols-sm="2"
                label="Date :"
                label-align-sm="right"
                label-for="nested-city"
            >
                <b-form-input class="w-75" type="date" id="nested-city"></b-form-input>
            </b-form-group>

            <b-form-group
                label-cols-sm="2"
                label="Time :"
                label-align-sm="right"
                label-for="nested-state"
            >
                <b-form-input class="w-75" type="time" id="nested-state"></b-form-input>
            </b-form-group>

            <b-form-group
                label-cols-sm="2"
                label="Ship via :"
                label-align-sm="right" class="mb-0"
            >
                <b-form-radio-group
                class="pt-2"
                :options="['Air', 'Courier', 'Mail']"
                ></b-form-radio-group>
            </b-form-group>
            <b-form-group
                label-cols-sm="2"
                label=""
                label-align-sm="" class="mx-auto mt-5"
            >
            <b-button class="mr-5"  variant="outline-danger">Danger</b-button>
            <b-button variant="primary">Primary</b-button>
            </b-form-group>
            </b-form-group>
        </b-card>
        </b-container>
    </section>
</template>

<script>
export default {
  name: 'Form',
  data () {
    return {
      street: ''
    }
  },
  computed: {
    borderColor () {
      const length = this.street.length
      return (length === 0) ? 'border-dark' : (length >= 3 && length < 15) ? 'is-valid' : 'is-invalid'
    }
  }
}
</script>
